1 {\r\n    font-size: 2.5rem;\r\n    color: white;\r\n    font-family: 'Inter', sans-serif;\r\n    font-weight: 400;\r\n}\r\n\r\nh2 {\r\n    font-size: 5rem;\r\n    color: black;\r\n    font-family: 'Bebas Neue', cursive;\r\n    font-weight: 500;\r\n    letter-spacing: 1px;\r\n}\r\n\r\nh3 {\r\n    font-size: 3rem;\r\n}\r\n\r\nh4 {\r\n    font-size: 2.5rem;\r\n}\r\n\r\np {\r\n    font-size: 1.6rem;\r\n    margin-bottom: 10px;\r\n    margin-top: 10px;\r\n}\r\n\r\nli {\r\n    font-size: 1.8rem;\r\n    list-style: none;\r\n}\r\n\r\na {\r\n    text-decoration: none;\r\n}\r\n\r\nspan {\r\n    font-weight: 900;\r\n}\r\n\r\nheader {\r\n    height: 200px;\r\n    position: relative;\r\n\r\n    background: linear-gradient(90deg, \r\n    #500052 12.77%, \r\n    #29022E 57.89%, \r\n    #000409 77.42%);\r\n\r\n    margin-bottom: 20px;\r\n}\r\n\r\nheader h2 {\r\n    color: white;\r\n}\r\n\r\n.contain-logos-mobile {\r\n    display: none;\r\n    transition: 0.2s;\r\n}\r\n\r\n.contain-all-logo img {\r\n    height: 120px;\r\n    transition: 0.2s;\r\n}\r\n\r\n.contain-all-logo {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n\r\n    margin: 0 6%;\r\n\r\n    position: absolute;\r\n    top: 10%;\r\n    right: 0;\r\n    left: 0;\r\n\r\n}\r\n\r\n.contain-logo {\r\n    background-color: white;\r\n    border-radius: 100%;\r\n    height: 110px;\r\n\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n}\r\n\r\n.page-title {\r\n    display: flex;\r\n    align-items: center;\r\n    width: 66%;\r\n    gap: 24px;\r\n}\r\n\r\n.title {\r\n    display: flex;\r\n    flex-direction: column;\r\n\r\n    gap: 10px;\r\n}\r\n\r\n.menu-container {\r\n    background: var(--menu-bar);\r\n    height: 45px;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n\r\n    position: absolute;\r\n    bottom: 0;\r\n    left: 0;\r\n    right: 0;\r\n}\r\n\r\n.menu {\r\n    position: sticky;\r\n    z-index: 5;\r\n}\